.favorite-tag__list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  color: rgba(18, 20, 23, 0.5);
  font-family: Manrope;
  font-size: 12px;
  font-weight: 400;
}
.favorite-tag__item {
  border-right: 1px solid rgba(18, 20, 23, 0.5);
  padding-right: 5px;
}
.favorite-tag__item:last-child {
  border-right: none;
}
.favorite-image {
  width: 100%;
  height: 264px;
  display: block;
  object-fit: cover;
  border-radius: 14px;
}
.favorite-svg {
  width: 14px;
  height: 14px;
  cursor: pointer;
  stroke: gold;
  position: absolute;
  z-index: 5;

  right: 10px;
  top: 10px;
}
.favorite-svg:focus,
.favorite-svg:hover {
  stroke: red;
}
.favorite-price {
  display: flex;
  justify-content: space-between;
}
.favorite-list {
  margin-top: 50px;
  margin-bottom: 40px;
  display: flex;
  gap: 29px;
  flex-wrap: wrap;
  justify-content: center;
}
.favorite-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: calc((100% - 87px) / 4);
  position: relative;
  height: 440px;
}
.favorite-button {
  display: flex;
  height: 44px;
  padding: 12px 99px;
  margin-top: 28px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;

  border-radius: 12px;
  cursor: pointer;
  border: none;
  background: #3470ff;

  color: #fff;
  font-family: Manrope;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.favorite-button:hover,
.favorite-button:focus {
  background-color: #0b44cd;
}
